html{ font-family: sans-serif; line-height: 1.15; }
body{ margin: 0px; }
 header, nav, section{ display: block; }
h1{ font-size: 2em; margin: 0.67em 0px; }
 main{ display: block; }
a{ background-color: transparent; }
 strong{ font-weight: bolder; }
img{ border-style: none; }
button, input{ font-family: inherit; font-size: inherit; font-style: inherit; font-variant: inherit; font-weight: inherit; line-height: inherit; margin: 0px; }
button, input{ overflow: visible; }
button{ text-transform: none; }
 [type="submit"], button{ -webkit-appearance: button; }
[type="search"]{ -webkit-appearance: textfield; outline-offset: -2px; }
@media print{
*{ color: rgb(0, 0, 0) !important; text-shadow: none !important; background-color: transparent !important; box-shadow: none !important; background-position: initial initial !important; background-repeat: initial initial !important; }
a{ text-decoration: underline; }
a[href]::after{ content: ' (', attr(href), ')'; }
a[href^="#"]::after{ content: ''; }
img{ page-break-inside: avoid; }
img{ max-width: 100% !important; }
 .h2, h1, p{ orphans: 3; widows: 3; }
 .h2, h1{ page-break-after: avoid; }
 .c-header{ display: none; }
}
html{ box-sizing: border-box; }
*{ box-sizing: inherit; }
 button, h1, input, p, ul{ margin-top: 0px; margin-bottom: 1rem; }
@media screen and (min-width: 50em){
 button, h1, input, p, ul{ margin-bottom: 1.25rem; }
}
@media screen and (min-width: 70em){
 button, h1, input, p, ul{ margin-bottom: 1.5rem; }
}
img{ height: auto; max-width: 100%; vertical-align: middle; }
html{ height: 100%; font-size: 16px; }
body{ height: 100%; font-family: LatoWeb, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Fira Sans', 'Droid Sans', 'Helvetica Neue', Arial, sans-serif; text-rendering: optimizelegibility; transition: all 0.15s ease-in-out; -webkit-transition: all 0.15s ease-in-out; position: relative; font-size: 1rem; line-height: 1.618; color: rgb(13, 13, 13); }
 p, ul{ font-size: 1.0625rem; }
@media screen and (min-width: 50em){
 p, ul{ font-size: 1.1875rem; }
}
@media screen and (min-width: 60em){
