 ul li{ padding-left: 0.25rem; }
 .giga, .h2, h1{ font-family: usual, LatoWeb, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Fira Sans', 'Droid Sans', 'Helvetica Neue', Arial, sans-serif; text-rendering: optimizelegibility; font-weight: 600; line-height: 1.25; }
.giga{ font-size: 3rem; }
@media screen and (min-width: 50em){
.giga{ font-size: 3.5rem; }
}
 h1{ font-size: 2rem; }
@media screen and (min-width: 50em){
 h1{ font-size: 2.25rem; }
}
.h2{ font-size: 1.625rem; }
@media screen and (min-width: 50em){
.h2{ font-size: 2rem; }
}
a{ transition: color 0.15s ease-in-out; -webkit-transition: color 0.15s ease-in-out; }
a{ color: rgb(178, 30, 40); }
label{ display: block; margin-bottom: 0.125rem; font-size: 1rem; }
@media screen and (min-width: 50em){
label{ margin-bottom: 0.25rem; font-size: 1.0625rem; }
}
button, input[type="search"]{ display: block; width: 100%; padding: 0.5rem 0.75rem; line-height: normal; background-color: rgb(255, 255, 255); border: 1px solid rgb(184, 184, 184); border-top-left-radius: 2px; border-top-right-radius: 2px; border-bottom-right-radius: 2px; border-bottom-left-radius: 2px; box-sizing: border-box; }
input[type="search"]{ -webkit-appearance: none; }
.o-btn{ display: inline-block; width: auto; height: auto; padding: 0.25rem 0.75rem; margin: 0px 0px 0.25rem; font-family: usual, LatoWeb, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Fira Sans', 'Droid Sans', 'Helvetica Neue', Arial, sans-serif; text-rendering: optimizelegibility; font-size: 1.0625rem; font-weight: 300; line-height: 1.618; letter-spacing: 0.5px; color: rgb(255, 255, 255); text-align: center; text-decoration: none; text-shadow: none; vertical-align: middle; cursor: pointer; background-color: rgb(178, 30, 40); border: 1px solid rgb(160, 27, 36); border-top-left-radius: 4px; border-top-right-radius: 4px; border-bottom-right-radius: 4px; border-bottom-left-radius: 4px; box-shadow: none; -webkit-user-select: none; transition: all 0.15s ease-in-out; -webkit-transition: all 0.15s ease-in-out; }
