.o-btn--reset{ width: auto; margin: 0px; }
.o-btn--reset{ color: rgb(13, 13, 13); background-color: transparent; border-color: rgb(13, 13, 13); }
.o-flag{ display: table; width: 100%; }
.o-flag__fix, .o-flag__flex{ display: table-cell; vertical-align: middle; }
.o-flag__fix{ padding-right: 10px; }
.o-flag__fix > *{ display: block; max-width: none; white-space: nowrap; }
.o-flag--rev .o-flag__fix{ padding-left: 10px; }
.o-flag__flex{ width: 100%; }
.o-container{ margin-right: auto; margin-left: auto; padding-right: 1rem; padding-left: 1rem; max-width: 90rem; }
.o-container::after{ display: table; clear: both; content: ''; }
.o-container--full{ max-width: 100%; }
.c-header{ position: fixed; top: 0px; right: 0px; left: 0px; z-index: 50; width: 100%; background-color: rgb(46, 46, 46); }
body.home .c-header{ background-color: transparent; transition: background 0.5s ease-in-out; -webkit-transition: background 0.5s ease-in-out; }
body.home .c-header::before{ position: absolute; top: 0px; right: 0px; bottom: -3rem; left: 0px; z-index: -1; display: block; background-image: linear-gradient(rgba(12, 12, 12, 0.6), rgba(12, 12, 12, 0)); content: ''; opacity: 1; transition: all 0.5s ease-in-out; -webkit-transition: all 0.5s ease-in-out; background-position: initial initial; background-repeat: initial initial; }
.c-header .o-container{ position: relative; z-index: 10; padding: 0.75rem; }
@media screen and (min-width: 60em){
.c-header .o-container{ padding: 0.125rem 1rem; }
}
.c-logo{ position: relative; z-index: 10; display: inline-block; width: 2rem; height: 2rem; line-height: 0; }
@media screen and (min-width: 40em){
.c-logo{ width: 2.625rem; height: 2.625rem; }
}
@media screen and (min-width: 60em){
.c-logo{ width: 2rem; height: 2rem; margin: 0.6875rem 0px; }
}
.c-main{ padding-top: 3.5rem; }
@media screen and (min-width: 40em){
.c-main{ padding-top: 4.125rem; }
}
@media screen and (min-width: 60em){
.c-main{ padding-top: 3.625rem; }
}
.home .c-main{ padding-top: 0px; }
.c-menu{ padding: 0px; margin: 0px; list-style-type: none; }
