.c-menu{ padding-left: 0.5rem; }
}
.c-menu .menu-item{ position: relative; display: block; padding: 0px; margin-bottom: 1rem; }
@media screen and (min-width: 60em){
.c-menu .menu-item{ display: inline-block; margin-bottom: 0px; float: left !important; }
}
.c-menu .menu-item a{ position: relative; display: block; padding: 0.5rem 0px 0.125rem; font-family: usual, LatoWeb, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Fira Sans', 'Droid Sans', 'Helvetica Neue', Arial, sans-serif; text-rendering: optimizelegibility; font-size: 0.9375rem; font-weight: 300; letter-spacing: 1px; color: rgb(115, 115, 115); text-decoration: none; text-transform: uppercase; }
.c-menu .menu-item a[href]{ color: rgb(207, 207, 207); }
@media screen and (min-width: 60em){
.c-menu .menu-item a{ padding: 1rem 0.5rem; letter-spacing: 0.5px; color: rgb(232, 232, 232); text-transform: none; font-size: 0.875rem; }
body.home .c-menu .menu-item a{ text-shadow: rgb(13, 13, 13) 0px 0px 3px; transition: text-shadow 0.15s ease-in-out; -webkit-transition: text-shadow 0.15s ease-in-out; }
.c-menu .menu-item a[href]{ color: rgb(232, 232, 232); }
}
.c-menu .sub-menu{ padding: 0.25rem 0px; margin: 0px; list-style-type: none; }
@media screen and (min-width: 60em){
.c-menu .sub-menu{ position: absolute; display: none; width: 15rem; background-color: rgb(13, 13, 13); }
}
.c-menu .sub-menu .menu-item{ display: block; margin-bottom: 0px; float: none !important; }
.c-menu .sub-menu .menu-item a{ padding: 0px; font-family: LatoWeb, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Fira Sans', 'Droid Sans', 'Helvetica Neue', Arial, sans-serif; text-rendering: optimizelegibility; font-size: 1.0625rem; font-weight: 400; letter-spacing: 0px; color: rgb(242, 242, 242); text-transform: none; }
@media screen and (min-width: 60em){
.c-menu .sub-menu .menu-item a{ padding: 0.125rem 0.5rem 0.0625rem; color: rgb(207, 207, 207); cursor: pointer; }
}
.c-menu .sub-menu .menu-item a::after{ display: none; content: ''; }
@media screen and (min-width: 60em){
.c-menu .sub-menu .menu-item a{ font-size: 0.9375rem; }
}
.c-navigation{ position: fixed; top: 0px; right: -15rem; bottom: 0px; width: 15rem; height: 100%; padding: 0.5rem 1rem 1rem; overflow: auto; background-color: rgb(46, 46, 46); }
@media screen and (min-width: 25em){
.c-navigation{ right: -18rem; width: 18rem; }
}
@media screen and (min-width: 60em){
.c-navigation{ position: relative; top: auto; right: auto; bottom: auto; width: auto; height: auto; padding: 0px; overflow: visible; background-color: transparent; }
}
button.c-nav-toggle{ padding: 0.8125rem 0.6875rem; margin: -0.5rem; font-size: 1.375rem; line-height: 0; background-color: transparent; border: 0px; background-position: initial initial; background-repeat: initial initial; }
@media screen and (min-width: 40em){
button.c-nav-toggle{ padding: 0.9375rem; font-size: 1.75rem; }
}
@media screen and (min-width: 50em){
button.c-nav-toggle{ padding: 6px 9px; margin: 1px 0px -7px; }
}
.c-navigation--toggle{ position: absolute; top: 0px; right: 0px; bottom: 0px; left: 0px; z-index: 100; display: none; width: 100%; height: 100%; background-color: rgb(13, 13, 13); opacity: 0; transition: all 0.15s ease-in-out; -webkit-transition: all 0.15s ease-in-out; }
.c-notification{ max-height: 0px; overflow: hidden; color: rgb(255, 255, 255); background-color: rgb(160, 27, 36); transition: all 0.15s ease-in-out; -webkit-transition: all 0.15s ease-in-out; }
.c-notification .o-container{ position: relative; padding: 0.75rem; }
@media screen and (min-width: 60em){
.c-notification .o-container{ padding: 1rem; }
}
.c-notification p{ margin: 0px; font-family: usual, LatoWeb, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Fira Sans', 'Droid Sans', 'Helvetica Neue', Arial, sans-serif; text-rendering: optimizelegibility; }
.c-notification p a{ color: rgb(255, 255, 255); }
.c-notification.is-notification-active{ max-height: 1000px; overflow: hidden; }
.c-notification .o-flag__fix{ padding-right: 0px; }
.c-notification__dismiss{ padding: 0px; margin: 0px; font-size: 2rem; font-weight: 100; line-height: 0; color: rgb(255, 255, 255); border: 0px; text-decoration: none !important; }
.c-search{ position: relative; width: 100%; max-width: 40rem; margin: 0px auto; }
@media screen and (min-width: 60em){
.c-navigation .c-search{ width: 10rem; }
}
@media screen and (min-width: 70em){
.c-navigation .c-search{ width: 12rem; }
}
.c-header .c-search{ margin: 0.5rem 0px; }
input.c-search__input{ padding: 0.75rem; line-height: 1.35; color: rgb(13, 13, 13); border: 1px solid rgb(207, 207, 207); transition: all 0.15s ease-in-out; -webkit-transition: all 0.15s ease-in-out; }
.c-navigation input.c-search__input{ border: 0px; }
@media screen and (min-width: 60em){
input.c-search__input{ padding: 0.5625rem 0.5625rem 0.4375rem; }
.c-navigation input.c-search__input{ color: rgb(255, 255, 255); background-color: rgb(23, 23, 23); }
}
button.c-search__button{ position: absolute; top: 0px; right: 0px; bottom: 0px; width: 43px; height: 43px; min-width: 0px; padding: 0px; margin: 0px; line-height: 0; color: rgb(115, 115, 115); background-color: transparent; border-width: 0px; border-top-left-radius: 0px; border-top-right-radius: 2px; border-bottom-right-radius: 2px; border-bottom-left-radius: 0px; transition: all 0.15s ease-in-out; -webkit-transition: all 0.15s ease-in-out; }
@media screen and (min-width: 60em){
button.c-search__button{ width: 38px; height: 37px; }
}
.c-section{ position: relative; width: 100%; padding: 1rem 0px; background-size: cover; background-position: 50% 50%; background-repeat: no-repeat no-repeat; }
@media screen and (min-width: 50em){
.c-section{ padding: 1.5rem 0px; }
}
@media screen and (min-width: 60em){
.c-section{ padding: 2rem 0px; }
}
@media screen and (min-width: 70em){
.c-section{ padding: 3rem 0px; }
}
.c-section > .o-container{ position: relative; }
.c-section--hero{ display: table; min-height: 70vh; padding: 2rem 0px; transition: all 0.15s ease-in-out; -webkit-transition: all 0.15s ease-in-out; }
@media screen and (min-width: 50em){
.c-section--hero{ padding: 4rem 0px; }
}
@media screen and (min-width: 60em){
.c-section--hero{ padding: 5rem 0px; }
}
@media screen and (min-width: 70em){
.c-section--hero{ padding: 6rem 0px; }
}
.c-section--hero > .o-container{ position: relative; display: table-cell; vertical-align: middle; }
.c-section--top > .o-container{ padding-top: 1rem; }
@media screen and (min-width: 40em){
.c-section--top{ max-height: 60vh; }
}
.c-section--overlay .o-container{ z-index: 10; }
.c-section--overlay::after{ position: absolute; top: 0px; right: 0px; bottom: 0px; left: 0px; z-index: 0; display: block; width: 100%; height: 100%; background-color: rgba(46, 46, 46, 0.85098); content: ''; }
.c-section--overlay--red::after{ background-color: rgba(178, 30, 40, 0.85098); }
.u-cf::after{ display: table; clear: both; content: ''; }
.u-pull-left{ float: left !important; }
.u-pull-right{ float: right !important; }
@media screen and (min-width: 60em){
.u-pull-left\@lg{ float: left !important; }
.u-pull-right\@lg{ float: right !important; }
}
.u-show{ display: block; }
@media screen and (min-width: 60em){
.u-hide\@lg{ display: none; }
}
.u-text-center{ text-align: center !important; }
.u-text-uppercase{ text-transform: uppercase; }
.u-text-white{ color: rgb(242, 242, 242); }
.u-text-light{ font-weight: 300; }
.u-margin{ margin-bottom: 1rem; }
 .u-margin-none{ margin-bottom: 0px !important; }
@media screen and (min-width: 60em){
 .u-margin-none\@lg{ margin-bottom: 0px !important; }
}
.u-hidden{ position: absolute; width: 1px; height: 1px; padding: 0px; margin: -1px; overflow: hidden; clip: rect(0px 0px 0px 0px); border: 0px; }